  --shark: #252525;
  --silver: #bcbcbc;
  --star-dust: #a1a1a1;
  --storm-dust: #606060;
  --white: #ffffff;
  --woodsmoke: #111010;
  --woodsmoke-2: #11101080;
 
  --font-size-l: 26px;
  --font-size-m: 20px;
  --font-size-s: 18px;
  --font-size-xl: 44px;
  --font-size-xs: 16px;
  --font-size-xxs: 14px;
  --font-size-xxxs: 9px;
 
  --font-family-inter-regular: "Inter-Regular", Helvetica;
  --font-family-poppins: "Poppins", Helvetica;
  --font-family-proxima_nova-bold: "Proxima Nova-Bold", Helvetica;
  --font-family-proxima_nova-regular: "Proxima Nova-Regular", Helvetica;
  --font-family-proxima_nova-semibold: "Proxima Nova-Semibold", Helvetica;
  --font-family-roboto: "Roboto", Helvetica;
}
